





























* {
  margin: 0;
  padding: 0;
}
.main {
  height: 50px;
  position: relative;
  background-color: #058CE8;
}
.main .tab {
  position: absolute;
  left: 10%;
  width: 60%;
  height: 50px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.main .tab .tab-item {
  height: 50px;
  width: 15%;
  line-height: 50px;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  text-align: center;
}
.main .tab .tab-item > a {
  display: block;
  font-size: 15px;
  color: white;
  text-decoration: none;
}
.main .tab .tab-item > a:active {
  color: #f01414;
}
.main .tab .tab-item > a:hover {
  background-color: #009688;
}
.main .tab .long {
  width: 25%;
}
.main .search {
  position: absolute;
  left: 70%;
  width: 20%;
  height: 50px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 7px 0;
}
.main .search .input {
  height: 36px;
  width: 60%;
  padding: 0 5%;
}
.main .search .input input {
  height: 32px;
  width: 100%;
}
.main .search .img {
  height: 36px;
  width: 30%;
}
.main .search .img img {
  cursor: pointer;
}
















































































